* {
  box-sizing: border-box;
}
button:hover {
  box-shadow: none !important;
}
a {
  transition: 0.15s;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
#footer {
  margin-top: auto;
}
.page-title {
  font-size: 32px !important;
  color: #fff !important;
  text-shadow: 0 3px 3px rgba(0, 0, 0, 0.4);
  margin: 0 !important;
  font-weight: 900 !important;
}
.left-border {
  display: inline-block;
  padding-left: 15px;
  border-left: 5px solid #ff6600;
}
#content,
#content-full {
  padding: 10px 15px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.15);
}
#main-menu-con {
  text-align: left;
}
#wp-admin-bar-my-account img {
  display: none !important;
}
#main-menu-con {
  margin-top: 24px;
}
#container {
  overflow: visible !important;
}
/*==============================================================
*												front header slider
=============================================================== */
.front-header-slider .slick-list img {
  width: 100% !important;
  height: 100% !important;
  object-fit: cover !important;
}
.front-header-slider .slider-link {
  display: block !important;
  width: 100%;
  height: 100%;
}
.front-header-slider .header-slider {
  display: block !important;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.front-header-slider .slick-list,
.front-header-slider .slick-track,
.front-header-slider .slider-item {
  height: 100%;
}
.front-header-slider .slider-item {
  position: relative;
}
.front-header-slider .slider-item:hover .label_text {
  transform: none;
  opacity: 1;
}
.front-header-slider .slider-item .label_text {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  opacity: 0;
  transform: translateY(30px);
  transition-duration: 0.3s;
  text-align: center;
  color: #fff;
  background-color: #3b7828;
  padding: 15px 20px;
}
.front-header-slider .slider-item .label_text .slider-title {
  font-size: 16px;
  margin-bottom: 0.8em;
}
.front-header-slider .slick-dots {
  position: absolute;
  z-index: 20;
  top: 0;
  right: 0;
  list-style: none;
  display: flex;
}
.front-header-slider .slick-dots button {
  flex-shrink: 0;
  font-size: 0;
  padding: 0;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  box-shadow: none;
  transition: 0.3s;
}
.front-header-slider .slick-dots .slick-active button {
  box-shadow: 0 0 0 3px #fff;
}
/*==============================================================
*												menu
=============================================================== */
.current-menu-item a {
  background: url("../images/nav-hover.png") no-repeat scroll center top transparent !important;
}
/*==============================================================
*												SIMPLE HEADER
=============================================================== */
.simple-header {
  width: 100%;
  height: 100%;
  position: relative;
}
.simple-header .bg-img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.simple-header .bg-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.simple-header .bg-img:before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.3);
}
.simple-header .head-content {
  padding: 25px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  display: flex;
  align-items: center;
}
/*==============================================================
*												HEADER
=============================================================== */
.social-link {
  display: block;
  width: 24px;
  height: 24px;
  background-size: 100% auto;
  background-position: center center;
  background-repeat: no-repeat;
}
.social-link.vk {
  background-image: url("../img/socials/vk.png");
}
.social-link.ok {
  background-image: url("../img/socials/ok.png");
}
.social-link.you {
  background-image: url("../img/socials/you.png");
}
.social-link.in {
  background-image: url("../img/socials/in.png");
}
.social-link.fb {
  background-image: url("../img/socials/fb.png");
}
#top-menu-container {
  display: flex;
  align-items: cetner;
  justify-content: space-between;
  margin-bottom: 45px;
  padding-top: 15px;
}
#searchform {
  min-width: 320px;
  padding-left: 10px;
  display: block;
}
#searchform input {
  width: 100% !important;
  margin: 0 !important;
}
#social {
  display: flex;
}
#social .social-link {
  display: block;
  margin-right: 15px;
  opacity: 0.6;
  transition: 0.3s;
}
#social .social-link:hover {
  opacity: 1;
}
#social .social-link:last-child {
  margin-right: 0;
}
/*==============================================================
*												PAGINATION
=============================================================== */
.navigation.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
}
.navigation.pagination .screen-reader-text {
  display: none !important;
}
.navigation.pagination .nav-links {
  display: flex;
}
.navigation.pagination .nav-links > * {
  display: block;
  padding: 3px;
  font-size: 14px;
  margin-right: 10px;
}
.navigation.pagination .nav-links > *:last-child {
  margin-right: 0;
}
/*==============================================================
*												ARCHIVE LOOP
=============================================================== */
.archive-loop .entrytext {
  display: flex;
}
.archive-loop .entrytext .shirt-desc {
  padding-left: 15px;
}
.post-loop-card {
  margin-bottom: 35px;
  transition: 0.3s;
  border-radius: 5px;
  background-color: #fff;
}
.post-loop-card:last-child {
  margin-bottom: 0;
}
.post-loop-card .post-title {
  padding: 15px 10px;
  border-radius: 5px 5px 0 0;
  background-color: #f60;
}
.post-loop-card .post-title a {
  color: #fff;
}
.post-loop-card .post-title a:hover {
  opacity: 0.6;
}
.post-loop-card .entrytext {
  margin: 0;
  padding: 15px 10px;
}
/*==============================================================
*												SEARCH CONTENT
=============================================================== */
.search-content .arc-post-title {
  margin-bottom: 20px;
}
.search-content form #searchsubmit[type="submit"] {
  width: 120px !important;
  display: block !important;
  margin-left: auto !important;
  margin-top: 20px !important;
}
.search-content .search-res-title {
  font-size: 24px;
  font-weight: bold;
  color: #414141;
  margin-bottom: 20px;
}
.search-content .search-res-title:hover {
  box-shadow: none !important;
}
/*==============================================================
*												NOT FOUND
=============================================================== */
.not-found {
  min-height: calc(100vh - 756px);
}
.not-found h2 {
  text-align: center;
  font-weight: bold;
  color: #158e09;
  text-shadow: 0 3px 3px #fff;
  font-size: 28px;
}
/*==============================================================
*												GALLERY
=============================================================== */
.gallery-page {
  display: flex;
  margin: 0 -15px;
  flex-wrap: wrap;
}
.gallery-page .gal-col {
  width: 50%;
  padding: 15px;
}
.gallery-page .gal-item {
  height: 20vw;
  position: relative;
  max-height: 230px;
  height: 230px;
  overflow: hidden;
}
.gallery-page .gal-item .gal-link {
  display: block;
  width: 100%;
  height: 100%;
}
.gallery-page .gal-item .gal-link img {
  width: 100% !important;
  height: 100% !important;
  object-fit: cover !important;
  max-width: none !important;
}
.gallery-page .gal-item:hover .gal-title {
  transform: none;
  opacity: 1;
}
.gallery-page .gal-title {
  position: absolute;
  padding: 10px;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  background-color: rgba(255, 102, 0, 0.7);
  opacity: 0;
  transition-duration: 0.3s;
  transform: translateY(15px);
}
